                style="background-color: rgba(0, 0, 0, 0.03)"
              >
                <i class="material-icons">info</i>
                <p class="mt-1">
                  A prorated refund of
                  <span class="font-semibold">{{ refundedProrateAmount }}</span>
                  will be credited to your account and applied to your next
                  billing cycle.
                </p>
              </div>
            </div>
            <Select
              v-if="!isMailinCurrentSelectedLocation"
              v-model="dataLocation.new_store_id"
              placeholder="Choose location"
              by="id"
              name="name"
              :items="listLocation"
              :loading="loadingLocations.all"
              max-height="200px"
              block
            >
              <template #prepend>
                <div class="px-4 py-4 w-full">
                  <Input
                    v-model="query.search"
                    placeholder="Search Location"
                    class="w-full"
                    block
                    icon
                  >
                    <template #icon>
                      <span class="material-icons">search</span>
                    </template>
                  </Input>
                </div>
              </template>
              <template #item="{ item }"> {{ item.name }} </template>
              <template #append>
                <div class="px-4 py-4 w-full flex flex-col">
                  <Pagination
                    v-model:page="query.page"
                    hide-show-entries
                    small
                    :data="stateLocation"
                  />
                </div>
              </template>
            </Select>
          </div>
          <Button
            color="red"
            class="mr-5"
            :loading="loading.delete || loading.mailin_location"
            :disabled="
              (loading.delete || dataLocation.new_store_id === null) &&
              !isMailinCurrentSelectedLocation
            "
            @click="deleteLocation"
          >
            Delete Location
          </Button>
          <Button
            color="white"
            :disabled="loading.delete"
            @click="modal.delete = false"
          >
            Cancel
          </Button>
        </div>
      </template>
    </Modal>
    <Modal v-model="isShowSwitchLocationModal" size="xs" hide-footer>
      <div class="flex flex-col items-center justify-center my-4">
        <img
          class="mb-4 w-28"
          src="@/assets/icon/unlock.svg"
